import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-genderselect',
  templateUrl: './genderselect.component.html',
  styleUrls: ['./genderselect.component.css']
})
export class GenderselectComponent implements OnInit {
  gender;
  showError
  constructor() { }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  save() {
    if(!this.gender){
      this.showError = true;
    } else {
      this.next.emit(({
        naxtPage: 'birthdate',
        model: this.gender,
        type: 'genderselect'
      }));
    }
  }

}



