import {Component, Input, OnInit} from '@angular/core';
import {animate, state, style, transition, trigger} from "@angular/animations";

@Component({
  selector: 'app-proposals',
  templateUrl: './proposals.component.html',
  styleUrls: ['./proposals.component.css'],
  animations: [
    trigger('expandCollapse', [
      state('open', style({
        'height': '*'
      })),
      state('close', style({
        'height': '0px'
      })),
      transition('open <=> close', animate(100))
  ])]
})
export class ProposalsComponent implements OnInit {

  data =
    [
      {
        "companyId": 1,
        "companyName": "Migdal",
        "companyLogoUrl": "images/results/logo-migdal.png",
        "priceForAllLife": 1500000,
        "pricesPerAges": [
          {
            "age": 40,
            "price": 34.22
          },
          {
            "age": 41,
            "price": 56.22
          },
          {
            "age": 42,
            "price": 70.22
          },
          {
            "age": 43,
            "price": 85.22
          }
        ]
      },
      {
        "companyId": 2,
        "companyName": "Menora",
        "companyLogoUrl": "images/results/logo-menora.png",
        "priceForAllLife": 43543543,
        "pricesPerAges": [
          {
            "age": 40,
            "price": 34.43
          },
          {
            "age": 41,
            "price": 55.45
          },
          {
            "age": 42,
            "price": 76.56
          },
          {
            "age": 43,
            "price": 87.67
          }
        ]
      },
      {
        "companyId": 3,
        "companyName": "Klal",
        "companyLogoUrl": "images/results/logo-klal.png",
        "priceForAllLife": 12323423,
        "pricesPerAges": [
          {
            "age": 40,
            "price": 23.22
          },
          {
            "age": 41,
            "price": 34.22
          },
          {
            "age": 42,
            "price": 54.22
          },
          {
            "age": 43,
            "price": 56.22
          }
        ]
      }
    ]
  openCloseAnim;
  hideText = true;
  showObj = {};

  constructor() {
    this.openCloseAnim = 'close';
  }
  openReportsFilter(i): void {
    for(let i = 0; i < this.data.length; i++){
      this.showObj[i] = false;
    }

    this.openCloseAnim = (this.openCloseAnim === 'open') ? 'close' : 'open';

    if(this.openCloseAnim === 'close'){
      setTimeout(
        () => {
          this.showObj[i] = false;
        }, 100
      );
    } else {
      this.showObj[i] = true;
    }

  }
  @Input() amount;

  ngOnInit() {
  }

}



