import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-calculatorsummary-modal',
  templateUrl: './calculatorsummary-modal.component.html',
  styleUrls: ['./calculatorsummary-modal.component.css']
})
export class CalculatorsummaryModalComponent implements OnInit {
  yearsData = [];
  monthData = [
    {
      label: "1",
      value: "1"
    },
    {
      label: "2",
      value: "2"
    },
    {
      label: "3",
      value: "3"
    },
  ];
  daysData = [];
  constructor() {
    this.setYear();
    this.setDays();
  }

  ngOnInit() {
  }

  @Output() close = new EventEmitter();

  setDays(){
    for (let i = 1; i <= 31; i++){
      this.daysData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }

  setYear() {
    for (let i = 1925; i <= 2018; i++){
      this.yearsData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }
  childrens = [];

  childrenCount = 0;

  doChangeChildren(){
    this.childrens = [];

    for(let i = 0; i < this.childrenCount; i++){
      this.childrens.push({stat: ""})
    }
  }

  closeClick(){
    this.close.emit("");
  }
  raiodVa;
  showError;

  save(){
    if(!this.raiodVa){
      this.showError = true;
    } else {
      this.close.emit("");
    }

  }

}



