import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-birthdate',
  templateUrl: './birthdate.component.html',
  styleUrls: ['./birthdate.component.css'],

})
export class BirthdateComponent implements OnInit {

  yearsData = [];
  monthData = [
    {
      label: "1",
      value: "1"
    },
    {
      label: "2",
      value: "2"
    },
    {
      label: "3",
      value: "3"
    },
  ];
  daysData = [];

  day = "1";

  month = "1";

  year = "1925";
  showError;
  showDrop;
  constructor() {
    this.setYear();
    this.setDays();
  }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  setDays(){
    for (let i = 1; i <= 31; i++){
      this.daysData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }

  setYear() {
    for (let i = 1925; i <= 2018; i++){
      this.yearsData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }



  daysChange(e){
    this.day = e;
  }

  monthChange(e){
    this.month = e;
  }

  yearsChange(e){
    console.log(e,2323)
    this.year = e;
  }

  back(){
    this.next.emit(({
      naxtPage: 'genderselect'
    }));
  }

  save() {
    if(!this.year ||
       !this.month ||
        !this.year){
      this.showError = true;
    } else {
    this.next.emit(({
      naxtPage: 'smoking',
      model: this.day + '-' + this.month + '-' + this.year,
      type: "birthdate"
    }));
    }
  }

}



