import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';

@Component({
  selector: 'app-autocomplit',
  templateUrl: './autocomplit.component.html',
  styleUrls: ['./autocomplit.component.css'],
  host: {
    '(document:click)': 'onClick($event)',
  },
})
export class AutocomplitComponent implements OnInit {

  label = "";

  searchModel = "";

  show;

  filteringData = [];

  constructor(private _eref: ElementRef) { }

  @Input() data;
  @Output() change = new EventEmitter();
  @ViewChild('searchInput') searchInput;

  ngOnInit() {
    if (this.data[0])
      this.label = this.data[0].label;
    this.filteringData = Object.assign([], this.data);

  }

  select(item){
    this.label = item.label;
    this.show = false;
    this.change.emit(item.value);
  }

  onSearchChange(value) {

    this.filteringData = Object.assign([], this.data);

    if(!value){
      return;
    }

    let filtering = [];
    for(let item of this.filteringData){
      if(item.label.indexOf(value) !== -1){
        filtering.push(item);
      }
    }
    this.filteringData = Object.assign([], filtering);


  }

  onClick(event) {
    if (!this._eref.nativeElement.contains(event.target)) // or some similar check
      this.show = false;
  }

  showClick(){
    this.show = !this.show;

    setTimeout(() => {
      this.searchInput.nativeElement.focus()
    }, 100);
  }
}



