import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-modal-chose',
  templateUrl: './modal-chose.component.html',
  styleUrls: ['./modal-chose.component.css']
})
export class ModalChoseComponent implements OnInit {

  constructor() { }

  @Output() closeEvent = new EventEmitter();

  ngOnInit() {
  }

  close(){
    this.closeEvent.emit("");
  }

  no(){
    this.closeEvent.emit("");
  }

  yes(){
    this.closeEvent.emit("");
  }

}



