import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-calculatorsummary',
  templateUrl: './calculatorsummary.component.html',
  styleUrls: ['./calculatorsummary.component.css']
})
export class CalculatorsummaryComponent implements OnInit {

  constructor() { }

  amount = 0;

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  save() {
    this.next.emit(({
      finish: true,
      amount: this.amount
    }));
  }
  back(){
    this.next.emit(({
      naxtPage: 'profession'
    }));
  }
}



