import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-profession',
  templateUrl: './profession.component.html',
  styleUrls: ['./profession.component.css']
})
export class ProfessionComponent implements OnInit {

  job = "בחר מקצוע";
  rate = "1000 - 10000";
  showError
  jobsData = [
    {
      label: "בחר מקצוע",
      value: "בחר מקצוע"
    },
    {
      label: "דייל/דיילת מכירו",
      value: "דייל/דיילת מכירו"
    },
  ];

  rateData = [
    {
      label: "1000 - 10000",
      value: "1000 - 10000"
    },
    {
      label: "10000 - 20000",
      value: "10000 - 20000"
    },
  ];

  constructor() { }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  jobsChange(e){
    this.job = e;
  }

  rateChange(e){
    this.rate = e;
  }

  save() {
    if(!this.job || !this.rate){
      this.showError = true;
    } else {
    this.next.emit(({
      naxtPage: 'calculatorsummary',
      type: 'profession',
      modal: {
      },
    }));
    }
  }

  back(){
    this.next.emit(({
      naxtPage: 'smoking'
    }));
  }

}



