import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.css']
})
export class InfoComponent implements OnInit {

  model = {
    firstName: "",
    lastName: "",
    phone: "",
    email: "",
    prefix: "050",
    link: true
  };

  phoneData = [
    {
      label: "050",
      value: "050"
    },
    {
      label: "040",
      value: "040"
    },
    {
      label: "060",
      value: "060"
    },
    {
      label: "070",
      value: "070"
    },
  ];
  showError
  constructor() { }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  phoneChange(e){
    this.model.prefix = e;
  }


  save() {

    console.log(this.model)
    if(this.model.phone === "" ||
      this.model.email === "" ||
      this.model.lastName === "" ||
      this.model.firstName === ""){
      this.showError = true;
    } else {
      this.next.emit(({
        naxtPage: 'genderselect',
        type: "info",
        model: this.model
      }));
    }
  }

}



