import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-smoking',
  templateUrl: './smoking.component.html',
  styleUrls: ['./smoking.component.css']
})
export class SmokingComponent implements OnInit {

  constructor() { }
  yearSmokin = "";
  smoking;
  showError;
  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  save() {
    if(!this.smoking && !(this.smoking === false)){
      this.showError = true;
    } else {
      if(this.smoking === false && !this.yearSmokin){
        this.showError = true;
        return;
      }

        this.next.emit(({
      naxtPage: 'profession',
      type: "smoking",
      model: this.smoking
    }));
    }
  }

  back(){
    this.next.emit(({
      naxtPage: 'birthdate'
    }));
  }
}



